body {
  --mainColor: #f51167;
}

#root {
  background-color: #fff3f387;
}

.card {
  transition: all ease-in-out 1s;
}

.card .parent-img {
  height: 450px;
}

.card-title {
  margin-bottom: 0;
}

.card .the-img {
  object-fit: cover;
  object-position: top;
}

.carousel-inner .carousel-item .info-slider .btn {
  padding: 18px 50px;
  font-size: larger;
  font-weight: 900;
}

.trash {
  transition: all ease-in-out 0.2s;
  position: absolute;
  top: 0;
  right: 0;
  width: 35px;
}

.trash .btn,
.like .btn {
  background-color: var(--mainColor);
}

.like {
  transition: all ease-in-out 0.2s;
  position: absolute;
  bottom: 0;
  left: -220px;
  width: 35px;
}
.card:hover .like {
  left: 0;
}

.card {
  overflow: hidden;
}

.card .like:hover,
.card .shopping-cart:hover,
.card .trash:hover {
  color: #fff;
  cursor: pointer;
}

.card .btn {
  padding: 7px 0px;
  width: 100%;
}

.card .btn .fa-heart,
.fa-heart-o {
  padding: 4px 30px;
}

.card .btn .fa-heart {
  color: #000;
}

.layoutForLike {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}

.layoutForLike .fa {
  font-size: 100px;
  font-weight: 900;
  position: absolute;
  top: 50%;
  margin-top: -35px;
  left: 50%;
  margin-left: -40px;
  opacity: 1;
  color: #dc3545;
}

.layoutForLike span {
  background-color: #00000034;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.shoppingCart {
  background-color: #282828;
  color: #fff;
  position: fixed;
  width: 40%;
  z-index: 9999;
  right: 0;
  top: 60px;
  padding: 30px;
  text-align: left;
  height: 100vh;
  transition: all ease-in-out 0.7s;
  box-shadow: 10px 10px 100px 30px #282828;
  border-radius: 5px;
  overflow-y: scroll;
  padding-bottom: 100px;
  transition: all ease-in-out 1s;
}

.item {
  height: 150px;
  width: 100%;
  padding: 15px;
}

.item .img-item {
  height: 100%;
  width: 25%;
  float: left;
  border-radius: 80px;
}

.item img {
  object-fit: cover;
  object-position: center;
  border-radius: 10px;
}

.item .counter-item {
  height: 100%;
  width: 25%;
  float: right;
}

.item .info-item {
  width: 50%;
  height: 100%;
  float: left;
  text-align: center;
}

.item .info-item .name-item {
  height: 40%;
  width: 100%;
  margin-bottom: 15px;
  float: left;
}

.item .info-item .price-item {
  height: 40%;
  margin-top: 15px;
  width: 100%;
  float: left;
}
.cartLength {
  background-color: var(--mainColor);
  position: relative;
  top: -15px;
  padding: 0 50px;
}

.navbar-one {
  padding: 1.6rem 1rem;
}

.shopping-cart .btn-warning {
  font-weight: 900;
  font-family: cursive;
}

.filters-buttons {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
}

.filters-buttons button {
  background-color: transparent;
  border: 3px solid var(--mainColor);
  color: #000;
  transition: all ease-in 0.3s;
}

.filters-buttons button.active {
  background-color: var(--mainColor);
  color: #fff;
}

.filters-buttons button:hover {
  background-color: var(--mainColor);
  color: #fff;
  border: 3px solid var(--mainColor);
}

select option {
  background-color: antiquewhite;
  cursor: pointer;
  color: #000;
}

::selection {
  background-color: black;
  color: antiquewhite;
}


