body {--mainColor: #f51167; overflow-x: hidden;}

.list-unstyled {
  text-align: center;
}

#root {
  font-family: Arial, Helvetica, sans-serif;

  overflow-x: hidden;
}

.list-unstyled li {
  display: inline-block;
}

.list-unstyled li:hover {
  cursor: pointer;
}

.slider {
  width: 100vw; 
  height: 100vh;
  position: relative;
}

.slider i.fa {
  position: absolute;
  font-size: 30px;
  font-weight: bold;
  color: var(--mainColor);
  padding: 20px 10px;
  border-radius: 10px;
  background-color: #fff;
  cursor: pointer;
}

.slider .fa-arrow-left {
  left: 50px;
  top: 35%;
}

.slider .fa-arrow-right {
  right: 50px;
  top: 35%;
}

header div .slider img{background-position: center; background-size: cover; background-attachment: fixed;}

.carousel-inner .carousel-item .info-slider {
  position: absolute;
  color: #fff;
  top: 20%;
  left: 10%;
  width: 40%;
}

.carousel-inner .carousel-item .info-slider .btn {
  background-color: transparent;
  border: solid 3px #000 ;
  padding: 10px 50px;
  color: #fff;
  transition: all ease-in-out .5s;
}

.carousel-inner .carousel-item .info-slider .btn:hover {
  background-color: var(--mainColor);
  border: solid 3px var(--mainColor) ;
}

.carousel-inner .carousel-item img {
  height: 100vh;
  object-fit: cover;
  object-position: center;
}

.carousel-inner .carousel-item h3 {
  font-family: serif;
  font-size: x-larger;
  padding-bottom: 15px;
}

.carousel-inner .carousel-item h1 {
  font-size: 3.5rem;
  font-weight: 900;
  font-family: system-ui;
  letter-spacing: 1.5px;
}

.carousel-inner .carousel-item p {
  font-size: large;
  padding-top: 15px;
  line-height: 1.8;
}

.carousel-control-prev-icon {
  font-size: 51px;
  padding: 29px;
  color: var(--mainColor);
}

.carousel-control-next-icon {
  font-size: 51px;
  padding: 29px;
  color: var(--mainColor) !important;
}
.header-rate:first-of-type {
  float: left;
  background-color: blue;
}

.header-rate:first-of-type {
  float: right;
  background-color: aqua;
}
