@media (max-width: 575.98px) { 

  .carousel-inner .carousel-item .info-slider {
    top: 11%;
    left: 19%;
    width: 60%;
  }

  .carousel-inner .carousel-item h3 {font-size: larger;}
  
  .carousel-inner .carousel-item h1 {font-size: 2rem;}
  
  .carousel-inner .carousel-item p {font-size: large;}

}

@media (min-width: 576px) and (max-width: 767.98px) { 

  .carousel-inner .carousel-item .info-slider {
    top: 30%;
    left: 19%;
    width: 70%;
  }

  .carousel-inner .carousel-item h3 {font-size: larger;}
  
  .carousel-inner .carousel-item h1 {font-size: 3rem;}
  
  .carousel-inner .carousel-item p {font-size: large;}

}

@media (min-width: 768px) and (max-width: 991.98px) { 

  .carousel-inner .carousel-item .info-slider {
    top: 30%;
    left: 15%;
    width: 70%;
  }

  .carousel-inner .carousel-item h3 {font-size: larger;}
  
  .carousel-inner .carousel-item h1 {font-size: 3.5rem;}
  
  .carousel-inner .carousel-item p {font-size: large;}
}

@media (min-width: 992px) and (max-width: 1199.98px) { 
  .carousel-inner .carousel-item img { object-position: left; }
}

@media (min-width: 1200px) { 
  .carousel-inner .carousel-item img {object-position: left;}


}
