footer {
  margin-top: 5%;
  width: 100%;
  background-color: #282828;
  color: #fff;
}
footer .logoDiv img {
  margin: 40px;
}

footer .container > .row {
  margin-top: 20px;
}

footer .container .row h4 {
  margin-bottom: 50px;
}

footer .container .row .about-footer p{
  color: #8f8f8f;
  padding-top: 10px;
}

.footer-widget ul li a:after {
  position: absolute;
  content: "";
  width: 5px;
  height: 5px;
  left: 0;
  top: 8px;
  border: 1px solid #ec105a;
  border-radius: 50%;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

.footer-widget ul {
  list-style: none;
  float: left;
}

.footer-widget ul li a {
  display: inline-block;
  position: relative;
  padding-left: 20px;
  font-size: 14px;
  color: #8f8f8f;
  margin-bottom: 6px;
  transition: all ease-in-out .3s;
}

.footer-widget ul li a:hover {
  color: #fff;
  text-decoration: none;
}

footer .container .row .third-section .img-question-info p:first-of-type{
  color: #8f8f8f;
  font-size: 15px;
  margin-bottom: 10px;
}

footer .container .row .third-section .img-question-info p:nth-of-type(2){
  color: #ccc;
  font-size: 14px;
  margin-bottom: 0;
}

footer .container .row .third-section .img-question-info p:last-of-type{
  color: #ec105a;
  font-size: 18px;
  margin-bottom: 40px;
}

footer .container .row .forth-section p{
  color: #8f8f8f;
  display: inline-block;
}

footer .container .row .forth-section p:nth-of-type(2){
  width: 85%;
}

footer .container .row .forth-section span{
  color: #ec105a;
  padding-right: 20px;
}

p {
  margin: 0;
}


.social-media div {
  cursor: pointer;
  padding: 10px;
  border-radius: 5px;
  transition: all ease-in-out .3s;
}

.instagram:hover {
  background-color: #e4405f;
}

.facebook:hover {
  background-color: #3b5999;
}

.twitter:hover {
  background-color: #55acee;
}

.youtube:hover {
  background-color: #cd201f;
}

.google:hover {
  background-color: #dd4b39;
}