
/*  This is The Main Color For my Website*/
.custom-container {
  width: 80%;
  margin: auto;
}

* {box-sizing: border-box; margin: 0; padding: 0;}

body { --mainColor: #f51167; overflow-x: hidden; padding-top: 60px; background-color: #f51167;}

nav {background-color: #ffe7e7; width: 100%; }

nav .logoImg {height: 40px;}

nav .search-nav {
  width: 40%;
  position: relative;
  text-align: right;
}

nav .form-control {
  background-color: #fff;
  width: 100%;
  border-radius: 20px;
}

nav .search-nav i {
  position: absolute;
  right: 40px;
  font-weight: bold;
  top: 10px;
}

nav .right-nav {line-height: 2.5; text-align: right;}

nav .right-nav p {
  display: inline-block;
  padding: 4px 20px 0px 5px;
  margin: 0;
  cursor: pointer;
}


.navbar-two {
  height: 31px;
  position: relative;
  top: 0px;
  z-index: 8;
}

.navbar-two a {
  color: #fff;
  padding-right: 25px;
}


.badge-new {
  background-color: var(--mainColor);
  position: absolute;
  top: 4px;
  right: 0px;
}

.navbar-two ul li{
  padding-left: 0px;
  position: relative;
  color: #282828;
}

.navbar-two ul li a {
  color: #282828;
  text-align: center;
  font-size: 18px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-decoration: none;
  transition: all ease-in-out .3s;
  padding: 17px 15px 0 15px;
  font-weight: bold;
}

select option {
  background: #000; 
  color: #fff; 
  box-shadow: inset 20px 20px var(--mainColor)
} 

select option:hover {
  color: #000; 
  box-shadow: inset 20px 20px var(--mainColor);
}

.navbar-effect {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  background-color: var(--mainColor);
  transition: all ease-in-out .5s;
}

.navbar-two ul li:hover .navbar-effect{width: 100%; height: 4px;}


.navbar-one {
  position: fixed;
  z-index: 9;
  top: 0;
}
  

